import React, { useLayoutEffect } from "react";

function Main() {
  useLayoutEffect(() => {
    // DON'T EDIT BELOW THIS LINE
    var d = document,
      s = d.createElement("script");
    s.src = "https://im3-fe-1.disqus.com/embed.js";
    let x = new Date().getTime();
    s.setAttribute("data-timestamp", `${x}`);
    (d.head || d.body).appendChild(s);

    return () => {};
  });
  return (
    <>
      <h1>삼피의 UI개발 공부</h1>
      <h3>
        <i className="fas fa-user-alt"></i> 삼피{" "}
        <i className="far fa-calendar-alt"></i> 2021.01.07
      </h3>
      <hr />
      <br />
      NTS UI개발 인턴을 하면서 공부한 내용을 바탕으로 연습하는 페이지입니다.
      <br />
      <br />
      <br />
      <hr />
      <div id="disqus_thread"></div>
    </>
  );
}

export default Main;
