import React, { useLayoutEffect } from "react";
import { Route } from "react-router-dom";
import Article from "./article";
import Main from "./main";
import "../css/article.scss";
function hide(e:any):void{
  document.querySelector('.artiList')?.classList.toggle('active')
}
function Webgl() {
  useLayoutEffect(() => {
    document.querySelector(".nav-b")?.classList.add("active");
    document.querySelector(".artiListHeader")?.addEventListener('click',hide)
        
    return () => {
      document.querySelector(".nav-b")?.classList.remove("active");
    };
  });
  return (
    <main>
      <aside>
        <h2 className="artiListHeader"><span>글 목록</span><i className="fas fa-sort-down"></i></h2> 
        <ul className="artiList">
          <li>1</li>
          <li>2</li>
          <li>3</li>
          <li>4</li>
          <li>5</li>
          <li>6</li>
          <li>7</li>
        </ul>
      </aside>
      <article>
        <Route path="/webgl/" exact component={Main} />
        <Route path="/webgl/:id/" exact component={Article} />
      </article>
      <div className="margin"></div>
    </main>
  );
}

export default Webgl;
